import { LanguageService } from 'src/app/sales/services/language.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { Component, Input, OnInit, SimpleChange } from '@angular/core';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';

@Component({
  selector: 'app-permission',
  templateUrl: './permission.component.html',
  styleUrls: ['./permission.component.scss']
})
export class PermissionComponent implements OnInit {
  freeAccount: any;
  permission: any = {}
  @Input() id_employee: any
  @Input() employee: any
  @Input() old_permission_group: any
  visibleLogin: boolean = false; //bin c show/hide phn quyn ng nhp
  visibleSale: boolean = false; //bin c show/hide phn quyn bn hng
  visibleWarranty: boolean = false; //bin c show/hide phn quyn bo hnh, sa cha
  visibleDelivery: boolean = false; //bin c show/hide phn quyn giao hng
  visibleCashflow: boolean = false; //bin c show/hide phn quyn dng tin
  visibleInvoice: boolean = false; //bin c show/hide phn quyn ha n
  visibleReport: boolean = false; //bin c show/hide phn quyn bo co
  visibleConfig: boolean = false; //bin c show/hide phn quyn cu hnh
  visibleManage: boolean = false; //bin c show/hide phn quyn qun l
  visibleNotifications: boolean = false; //bin c show/hide phn quyn thng bovisibleBranch
  visibleBranch: boolean = false; //bin c show/hide phn quyn thng bo branch
  visibleAdmin = false//bin c show/hide phn quyn admin
  default_price_type: any
  customer_management_scope : any
  isVisibleChooseCustomer = false;
  visibleCustomerScope= false; //bin c show/hide phn quyn CustomerScope
  list_customer = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).filter(item => item._id != 'id_retail')
  list_filter_customer = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3)
  list_all_customer =[]
  list_customer_class = this.vhQuerySales.getlocalCustomerClasss()
  filterCustomerClass = 'all'
  searchCustomerValue = ''
  constructor(
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhQuerySales :VhQuerySales,
    private vhAlgorithm :VhAlgorithm
    ) { }

  ngOnInit() {
    this.freeAccount = this.vhAuth.getStore().freeaccount
   
  }
  /**
   * get phn quyn ca nv 
   * @example this.getData()
   */
  getData() {
    // this.vhComponent.showLoading('').then(()=>{
    this.vhAuth.getPermission(this.id_employee)
      .then((permission) => {
        if (permission)
          this.permission = permission;
        this.checkPermissionAdmin();
        this.checkPerrmissionDefaultPriceType();
        this.checkPermissionDiscountAndCommision();
        // this.vhComponent.hideLoading(0)
        if (permission.default_price_type_1) this.default_price_type = 1
        if (permission.default_price_type_2) this.default_price_type = 2
      })
    // })
    // get danh sch kh  lm chc nng chn khch hng cho nv
      this.list_customer = this.employee.customers ? this.list_customer.map(item => { return {...item, checked: this.employee.customers.find(ele => ele == item._id) ? true : false}}) : this.list_customer 
      this.list_filter_customer = this.list_customer;
      this.list_all_customer = this.list_customer;
  }
  ngOnChanges(change: SimpleChange) {
    if (change['old_permission_group']) this.getData()
  }
  /**
   * cp nht permission theo trng truyn vo
   * @param value 
   * @example this.updatePermission({selling_display_page:true})
   */
  updatePermission(value) {
    this.vhAuth.updatePermission(this.id_employee, value);
  }

  /**
   * lc khch hng theo hng khch hng
   * @param event 
   * @example this.filterCustomer('all')
   */
  filterCustomer(event){
    if(event == 'all') this.list_filter_customer = this.list_all_customer
    else this.list_filter_customer = this.list_all_customer.filter( item => item.id_customer_class == event)
    this.searchCustomer(this.searchCustomerValue)
  }
  /**
   * lc khch hng theo ['name','phone','email']
   * @param value 
   * @example this.searchCustomer('')
   */
  searchCustomer(value){
    this.searchCustomerValue = value
    this.list_customer = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value), this.list_filter_customer,['name','phone','email'])
  }
  /**
   * m modal chn khch hng cho nv qun l
   * @example this.chooseCustomer()
   */
  chooseCustomer(){
    this.isVisibleChooseCustomer = true;

  }
  /**
   * tt modal chn khch hng cho nv qun l
   * @example this.handleCancel()
   */
  handleCancel(){
    this.isVisibleChooseCustomer = false
  }

  /**
   * x l nhn nt ok ca modal chn khch hng
   * @example this.handleOk()
   */
  handleOk(){
    
    let array_id_customer = this.list_customer.filter(item => item.checked).map(item => { return item._id})
    this.vhAuth.updateEmployee(this.id_employee, {customers : array_id_customer})
    .then((bool)=>{
      this.isVisibleChooseCustomer = false

       if(bool) {
         this.vhComponent.alertMessageDesktop("success",this.languageService.translate("Cp nht khch hng cho nhn vin thnh cng"))
       }
       else  this.vhComponent.alertMessageDesktop("error",this.languageService.translate("Cp nht khch hng cho nhn vin tht bi! Vui lng th li sau!!!"))
      })
  }

   
  /**
   * x l chn gi bn mc nh ca nv
   * @param ev 
   * @example this.changePriceType(1)
   */
  changePriceType(ev) {
    switch (ev) {
      case 1: { this.updatePermission({ default_price_type_1: true, default_price_type_2: false }); break }
      case 2: { this.updatePermission({ default_price_type_1: false, default_price_type_2: true }); break }
    }
  }

  /**
   * 2024 s xa hm ny, hm ny pht sinh sau khi public app 1 nm, trc y cha c permission ny nn phi check  update cho nv
   * @example thischeckPermissionDiscountAndCommision()
   */
  checkPermissionDiscountAndCommision(){
    if (this.permission.management_enable_sales_discount == undefined) {
      this.vhAuth.updatePermission(this.id_employee, { management_enable_sales_discount: false, management_enable_sales_invoice_discount: false, management_enable_sales_commission : false  })
        
    }
  }

  /**
   * 2024 s xa hm ny, hm ny pht sinh sau khi public app 1 nm, trc y cha c permission ny nn phi check  update cho nv
   * @example checkPerrmissionDefaultPriceType()
   */
  checkPerrmissionDefaultPriceType() {
    if (this.permission.default_price_type_1 == undefined) {
      this.vhAuth.updatePermission(this.id_employee, { default_price_type_1: true, default_price_type_2: false  })
        .then((bool) => {
          this.default_price_type = 1;
        })
    }
  }

  /**
   * 2024 s xa hm ny, hm ny pht sinh sau khi public app 1 nm, trc y cha c permission ny nn phi check  update cho nv
   * @example checkPermissionAdmin()
   */
  checkPermissionAdmin() {
    if (!this.permission.pos_display_page && !this.permission.desktop_display_page && !this.permission.admin_display_page) {
      this.vhAuth.updatePermission(this.id_employee, { pos_display_page: true, desktop_display_page: true, admin_display_page: true })
        .then((bool) => {
          this.permission.pos_display_page = true;
          this.permission.desktop_display_page = true;
          this.permission.admin_display_page = true;

        })
    }
  }

  /**
   * cp nht nv theo trng truyn vo
   * @param value 
   * @example this.updateEmployee({customers:[]})
   */
  updateEmployee(value){
    this.vhAuth.updateEmployee(this.id_employee, value).then((bool)=>{
      
      })
  }
}
